
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: justify;
  color: #2c3e50;
  margin-top: 60px;
}
#app .markdown {
    padding-left: 30px;
}
#app .markdown h1 {
      font-size: 28px;
      font-weight: 500;
      line-height: 40px;
      margin-bottom: 24px;
      margin-top: 8px;
}
#app .markdown h2, #app .markdown h3, #app .markdown h4, #app .markdown h5, #app .markdown h6 {
      margin: .3em 0 .6em;
      font-weight: 500;
      clear: both;
}
#app .markdown h2 {
      font-size: 22px;
}
#app .markdown h3 {
      font-size: 18px;
}
#app .markdown h4 {
      font-size: 16px;
}
#app .markdown h5 {
      font-size: 14px;
}
#app .markdown section > ul li {
      margin-left: 20px;
      padding-left: 4px;
      list-style: circle;
      line-height: 25px;
}
#app .markdown section > p {
      margin-bottom: 20px;
      line-height: 24px;
}
#app .markdown strong {
      font-weight: 500;
}
#app .markdown h2 a, #app .markdown h3 a, #app .markdown h4 a {
      font-size: .8em;
      opacity: 0;
      -webkit-transition: opacity .2s ease-in-out;
      transition: opacity .2s ease-in-out;
}
#app .markdown h2:hover a, #app .markdown h3:hover a, #app .markdown h4:hover a {
      opacity: 1;
}
#app .markdown section > table {
      border-collapse: collapse;
      border-spacing: 0px;
      empty-cells: show;
      border: 1px solid #e9e9e9;
      width: 100%;
      margin: 16px 0;
}
#app .markdown section > table th, #app .markdown section > table td {
        border: 1px solid #e9e9e9;
        padding: 8px 16px;
        text-align: left;
}
#app .markdown section > table th {
        background: #F7F7F7;
}
#app .list-group .menu {
    -webkit-box-shadow: none;
            box-shadow: none;
}
#app .header-anchor {
    margin-left: -18px;
}
#app .side-nav {
    border-right: 1px solid #e9e9e9;
}
#app pre code {
    background-color: #f8f8f8;
}
#app .footer {
    padding: 40px 20px;
}

